<!--
 * @Author: 
 * @Begin: 2019-09-08 14:37:08
 * @Update: 2019-11-12 21:42:40
 * @Update log: 
 -->
<template>
  <div class="img-card" @click="searchIdx(idx)" :style="{width, marginTop:top}">
    <span class="tag" v-if="playCount">
      <i class="card cardbofang"></i>
      {{playCount | setPlayCount}}
    </span>
    <span class="swiper-tag" v-if="swiper">
      <i class="card cardbofang1"></i>
    </span>
    <span class="fine-tag" v-if="fine">
      <i class="card cardhuangguan"></i>
    </span>
    <span class="time-tag" v-if="updateTime">{{ updateTime }}</span>
    <div class="img-con" :style="{width,paddingBottom:width}">
      <div class="shadow"></div>
      <!-- keynoneblock -->
      <img v-lazy="imgUrl + '?param=200y200'" :key="imgUrl" class="image" />
      <span class="dj-name" v-show="type === 'dj'">{{name}}</span>
      <!--  -->
      <!-- <router-link class="cover"  @click="toAlbum(albumId)" :to="'/albumPage/'+albumId"></router-link> -->
    </div>
    <div class="dec" :class="{lines:lines === 'one',twoLines:lines === 'two'}">{{ dec }}</div>
    <div class="artists" v-if="artists">
      <span v-for="(item, index) in artists" :key="index" class="artist">{{ item.name }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  props: {
    playCount: {
      type: Number
    },
    updateTime: {
      type: String
    },
    imgUrl: {
      type: String
    },
    dec: {
      type: String
    },
    type: {
      type: String
    },
    name: {
      type: String
    },
    width: {
      type: String,
      default: '2.1rem'
    },
    lines: {
      type: String,
      default: 'two'
    },
    artists: {
      type: Array
    },
    top: {
      type: String,
      default: '0.3rem'
    },
    // 
    swiper: {
      type: Boolean,
      default: false
    },
    fine: {
      type: Boolean,
      default: false
    },
    albumId: {
      type: Number
    },
    dishId: {
      type: Number
    },
    ridId: {
      type: Number
    },
    idx: {
      type: String
    }
  },
  filters: {
    setPlayCount: function (val) {
      if (!val) {
        return ''
      }
      if (val > 100000000) {
        val = ((val / 100000000).toFixed(1)) + ''
      } else if (val > 10000) {
        val = Math.floor(val / 10000) + ''
      }
      return val
    }
  },
  methods: {
    /**
     * 
     *
     * idxalbumId
     * idx
     */
    searchIdx (idx) {
      if (!idx) {
        if (this.albumId) {
          // [vue-router] Route with name 'albumPage' does not exist
          // name
          //  /:id
          this.$router.push({ name: 'albumPage', params: { albumId: this.albumId } })
          // this.$router.push(`/albumPage/${this.albumId}`)
          return
        }
        if (this.dishId) {
          this.$router.push({ name: 'albumPage', params: { dishId: this.dishId } })
          return
        }
        if (this.ridId) {
          console.log('go')

          this.$router.push({ name: 'djDetailPage', params: { ridId: this.ridId } })
          return
        }
        return
      }
      switch (idx) {
        case '':
          idx = 0
          break
        case '':
          idx = 1
          break
        case '':
          idx = 2
          break
        case '':
          idx = 3
          break
        case '':
          idx = 23
          break
        case 'ACG':
          idx = 22
          break
        case 'KTV':
          idx = 7
          break
        case 'iTunes':
          idx = 8
          break
        case 'Oricon':
          idx = 10
          break
        case 'Hit FM Top':
          idx = 9
          break
        case 'Hito':
          idx = 20
          break
        case 'Beatport':
          idx = 21
          break
        case ' NRJ Vos Hits ':
          idx = 20
          break
        case '':
          idx = 4
          break
        case 'UK':
          idx = 5
          break
        case 'Billboard':
          idx = 6
          break
        case '':
          idx = 24
          break
        case '':
          idx = 25
          break
        case '':
          idx = 26
          break
        case '':
          idx = 27
          break
        case '':
          idx = 28
          break
        case 'Q':
          idx = 29
          break
        case '':
          idx = 30
          break
        case '':
          idx = 31
          break
        case '':
          idx = 32
          break
        case 'TOP':
          idx = 33
          break
      }
      this.$emit('showIdxPage', idx)
    }
  }
}
</script>

<style lang='less' scoped>
@import url("//at.alicdn.com/t/font_1396631_tp8pq8axas.css");
@import url("~styles/global.less");
.img-card {
  position: relative;
  background-color: #fff;
  .time-tag {
    position: absolute;
    bottom: 0.54rem;
    left: 3px;
    font-size: 12px;
    color: #fff;
  }
  .tag {
    position: absolute;
    z-index: 1;
    top: 0.11rem;
    right: 0.11rem;
    font-size: 0.2rem;
    color: #fff;
    .cardbofang {
      font-size: 0.18rem;
    }
  }
  .swiper-tag {
    position: absolute;
    bottom: 1rem;
    right: 0.11rem;
    .cardbofang1 {
      font-size: 1rem;
      color: #fff;
      opacity: 0.6;
    }
  }
  .fine-tag {
    position: absolute;
    transform: rotate(-45deg);
    top: 0;
    left: 0;
    .cardhuangguan {
      color: #f39c12;
      font-size: 0.5rem;
    }
  }
  .img-con {
    position: relative;
    height: 0;
    background-color: #aaa;
    border-radius: 0.2rem;
    overflow: hidden;
    .shadow {
      position: absolute;
      height: 2.1rem;
      width: 100%;
      box-shadow: 0 15px 21px -9px #777 inset;
    }
    .dj-name {
      position: absolute;
      bottom: 8px;
      left: 8px;
      color: #fff;
      font-size: 0.2rem;
      width: 100%;
      .ellipsis();
    }
    img {
      width: 100%;
    }
  }
  .dec {
    margin-top: 0.2rem;
    font-size: 0.23rem;
    line-height: 0.3rem;
    letter-spacing: 1px;
    &.lines {
      .ellipsis();
      height: 0.3rem;
    }
    &.twoLines {
      height: 0.6rem;
      .twoLinesEllipsis();
    }
  }
  .artists {
    .ellipsis();
    .artist {
      font-size: 0.2rem;
      color: #aaa;
      &::after {
        content: "/";
      }
      &:last-child::after {
        content: "";
      }
    }
  }
}
</style>



